.main-footer {
  background-color: $gray-700;
  padding: 3.5rem 4rem 3rem;
  position: relative;
  .col-md-3{
    text-align: center;
  }

  h5 {
    color: #a6a6a6;
    display: inline;

    &:nth-child(2) {
      margin-left: 2rem;
    }
  }
}

.mail-link {
  font-size: 1.5rem;
  color: #dacfc6;
  word-break: break-word;
}

.social-icons {
  padding-top: 1.8rem;
  font-size: 1.8rem;

  li {
    a {
      i {
        color: #dacfc6;

        &:hover {
          color: $white;
        }
      }
    }
  }
}

.footer-logo {
  max-width: 17.75rem;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}